 *                eUI all versions | http://eui.ecdevops.eu
 * See also: https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css
 *
 * @history:
 *      28-11-2024 EUI-10382 - Added eui-u-display-print-hidden class for no print elements Hide scrollbars.
 *      18-03-2024 EUI-9059 - Allow the eui-block-content to print content even when isBlocked is true (hide only the spinner)
 *      20-10-2023 EUI-8258 - Removed all legacy ux references & align to new App-Shell (page) structure
 *      28-04-2022 EUI-5888 - Page with columns content display fix
 *      07-10-2020 EUI-3459 - Alignments on Bootstrap removal
 *      05-06-2020 EUI-2930 + See \src\styles\04-vendors\libs-overrides\bootstrap\_variables.scss
 *      20-02-2019 MWP-994
 *      18-02-2019 MWP-995
 *      13-12-2018 MWP-865
 *      23-08-2018 MWP-414
 *      20-06-2018 EUI-383
 *      15-06-2018 MWP-298
 *      07-06-2018 UXATEC-1508 (initial release)
*/
@use 'sass:map';
@use '../../../base/02-tokens/maps' as tokens;

$debug: false;
$print-page-size: auto !default;  // EUI-2930: fix page layout orientation (portrait or landscape)
$print-body-min-width: map.get(tokens.$breakpoint-map, 'l') !default;

@media print {
    *,
    *::before,
    *::after {
        // background: transparent !important;  // avoid in order to enable "background graphics print"
        box-shadow: none !important;
        // color: #000 !important; /* Black prints faster: http://www.sanbeiji.com/archives/953 */
        text-shadow: none !important;
    }

    // Remove links
    a,
    a:visited {
        text-decoration: none !important;

        &:not(.btn) {
            text-decoration: none !important;
        }
    }

    abbr[title]::after {
        content: " (" attr(title) ")";
    }

    // Don't show links that are fragment identifiers or use the `javascript:` pseudo protocol
    // a[href^="#"]::after,
    // a[href^="javascript:"]::after {
    //     content: "";
    // }

    pre {
        white-space: pre-wrap !important;
    }

    pre,
    blockquote {
        border: none !important;
        page-break-inside: avoid !important;
    }

    // Printing Tables: http://css-discuss.incutio.com/wiki/Printing_Tables
    thead {
        display: table-header-group !important;    // h5bp.com/t
    }

    //----------------------------------------
    // PAGE SETTINGS (widow & orphan's lines)
    // Specify a size and min-width to make printing closer across browsers.
    // We don't set margin here because it breaks `size` in Chrome.
    // Don't use `!important` on `size` as it breaks in Chrome.
    //----------------------------------------
    @page {
        size: $print-page-size; // EUI-2930: fixes page layout orientation (portrait or landscape)
        // margin-bottom: 1.5cm;
        // margin-top: 1.5cm;
        // size: A4;
    }
    @page :first {
        // first page pseudo-class selector
    }
    @page :left {
        // margin-left: 1cm;
    }
    @page :right {
        // margin-right: 1cm;
    }

    body {
        min-width: $print-body-min-width !important;
    }

    .container {
        min-width: $print-body-min-width !important;
    }

    p,
    blockquote,
    h2,
    h3 {
        orphans: 3 !important;  // no orphan line at the bottom
        widows: 3 !important;   // no orphan line at the top
    }

    // Prevent headings from being printed at the bottom of the page
    h1,
    h2,
    h3,
    caption {
        page-break-after: avoid !important;
    }

    // Prevent elements from being split up
    tr,
    img,
    pre,
    blockquote,
    figure {
        page-break-inside: avoid !important;
    }

    //------------------------
    // eUI COMPONENTS & MISC
    //------------------------

    // Watermark: add every top page content (QR code, logo, etc.)
    // #app-wrapper::after {
    //     content: url(https://chart.googleapis.com/chart?cht=qr&amp;chs=150x150&amp;chl=<?=$_SERVER["SERVER_NAME"].$_SERVER["REQUEST_URI"] ?>&amp;choe=UTF-8);
    //     position: absolute;
    //     right: 0;
    //     top: 0;
    // }

    // Elements to hide
    .d-print-none, .eui-u-display-print-hidden,
    .eui-overlay,
    .eui-growl,
    .eui-block-content--blocked::before,
    .eui-popover,
    .eui-button-fixed
    .eui-button-floating,
    .eui-u-hidden-desktop-down {
        display: none !important;
        visibility: hidden !important;
    }

    // Hide scrollbars
    ::-webkit-scrollbar {
        display: none !important;
    }


    // Removing Multimedia elements
    iframe {
        display: none !important;
        height: 0 !important;
        line-height: 0 px !important;
        overflow: hidden !important;
        white-space: nowrap;
        width: 0 !important;
    }

    audio,
    video,
    .embed-youtube,
    .embed-responsive {
        display: none !important;
        height: 0 !important;
        overflow: hidden !important;
        position: absolute !important;
    }

    // Visible elements
    #printSection {
        visibility: visible !important;
    }

    // Elements to print
    body,
    #app-wrapper,
    #main,
    .container,
    .panels,
    .d-print-inline,
    .d-print-inline-block,
    .d-print-block,
    .d-print-table,
    .d-print-table-row,
    .d-print-table-cell,
    .d-print-flex,
    .d-print-inline-flex {
        background-color: #fff !important;
        display: block !important;
        float: none !important;
        margin: auto !important;
        min-height: 100% !important;
        overflow: visible !important;   // Avoid last page to be cut (still bugged on Chrome)
        position: static !important;
        width: 100% !important;

        & .eui-u-flex,
        & .flex-container,
        & .d-flex
        & .row
        {
            display: flex !important;
        }
    }

    // Avoid last page to be cut (sometimes still bugged on Chrome)
    body {
        background-color: #fff !important;
        display: block !important;
        margin: 0 !important;
        padding: 0 !important;
    }

    // DISABLED - For elements to not truncate (remove ellipsis) use this:
    //     display: block !important;
    //     margin: 0 !important;
    //     overflow: visible !important;
    //     // padding: 0 !important;   // do not touch original padding
    //     page-break-inside: avoid !important;
    //     text-overflow: clip;    // removes ellipsis truncation if any
    // }

    // Miscellaneous
    * .eui-u-color-white,
    * .eui-u-color-grey-0 {
        color: #000 !important;
    }

    // REACTIVE FORMS
    .eui-input-group {
        align-items: flex-start !important;
        display: flex !important;
        flex-direction: column !important;
    }

    .row.eui-input-group {
        align-items: center !important;
        display: flex !important;
        flex-direction: row !important;

        > * {
            box-sizing: border-box !important;
            flex-shrink: 0 !important;
            width: auto !important;
            max-width: unset !important;
            padding-right: calc(var(--bs-gutter-x) * 0.5) !important;
            padding-left: calc(var(--bs-gutter-x) * 0.5) !important;
            margin-top: var(--bs-gutter-y) !important;
        }
    }

    // APP-SHELL NEXT (eUI 10+)
    .eui-app-main {
        background: var(--eui-c-white) !important;
        margin-left: 0 !important;
        width: 100% !important;

        .eui-app-main-content {
            margin-top: 0 !important;
        }

        .eui-app-toolbar {
            display: none !important;
        }
    }

    .cdk-overlay-container,
    .eui-app-sidebar,
    .eui-clearable-input-wrapper__clear {
        display: none !important;
    }

    .eui-u-display-print-only {
        display: block !important;
    }

    .eui-u-display-no-print {
        display: none !important;
        visibility: hidden !important;
    }

    // PAGE & COLUMNS (EUI-5888)
    // Always display overflowed / scrollable / not visible content
    .eui-page {
        &--with-columns {
            position: relative !important;
        }

        .eui-page-columns {
            overflow-y: visible !important;
        }

        .eui-page-column {
            min-height: 100% !important;
            overflow: visible !important;

            .eui-page-column__body {
                overflow: visible !important;
            }
        }
    }

    // SHOWCASE
    .doc-page-main-column {
        width: 100% !important;
    }
}
